export type QuestionResponseData = {
    id?: number;
    questions: Question[];
};

interface QuestionResponse {
    data: QuestionResponseData;
}

export const getQuestions = async (
    topicSlug?: string
): Promise<QuestionResponseData> => {
    let data: QuestionResponseData;
    if (!topicSlug) {
        data = {
            questions: homeQuestions,
            id: undefined,
        };
    } else {
        try {
            const response: QuestionResponse = await axios.get(
                `/api/${topicSlug}/quizzes/`
            );
            data = response.data;
        } catch (error) {
            console.error(error, error.stack);
            alert(
                "Some errors occured. Please check your internet connection."
            );
            return { id: undefined, questions: [] };
        }
    }
    return {
        id: data.id,
        questions: randomizeQuestions(data.questions),
    };
};

export const submitQuiz = async (
    quizSittingId: number,
    correctQuestion: number[]
): Promise<Topic[]> => {
    try {
        const response: { data: { topics: Topic[] } } = await axios.patch(
            `/api/quizzes/${quizSittingId}/`,
            {
                correct_questions: correctQuestion,
            }
        );
        return response.data.topics;
    } catch (error) {
        console.error(error, error.stack);
        return [];
    }
};
