import axios from "axios";
import { Topic } from "containers/Dashboard/dashboardSlice";
import { User } from "./authSlice";

type signUpResponseData = {
    user: User;
    auth_token: string;
    topics: Topic[];
};
type signUpResponse = {
    data: signUpResponseData;
};

export const signUpAPI = async (
    email: string,
    password: string,
    isSignUp: boolean
): Promise<signUpResponseData | undefined> => {
    const url = isSignUp ? "/auth/users/" : "/auth/token/login/";
    try {
        const response: signUpResponse = await axios.post(url, {
            email,
            password,
        });
        localStorage.setItem("token", response.data.auth_token);
        axios.defaults.headers = {
            Authorization: {
                toString() {
                    return `Token ${response.data.auth_token}`;
                },
            },
        };
        return response.data;
    } catch (error) {
        if (error.response?.data) throw error;
        console.error(error, error.stack);
        alert(
            "An error occured when processing your request. Please check your connection and try again."
        );
        return;
    }
};

export const bootstrapAPI = async (
    token: string
): Promise<signUpResponseData | undefined> => {
    try {
        const response: signUpResponse = await axios.get("/auth/users/me/", {
            headers: {
                Authorization: `Token ${token}`,
            },
        });
        axios.defaults.headers = {
            Authorization: {
                toString() {
                    return `Token ${response.data.auth_token}`;
                },
            },
        };
        return response.data;
    } catch (error) {
        console.error(error, error.stack);
        return;
    }
};

export const logOutAPI = () => {
    try {
        axios.post("/auth/token/logout/");
        localStorage.clear();
        axios.defaults.headers = {};
    } catch (error) {
        console.error(error, error.stack);
        alert(
            "An error occured when processing your request. Please check your connection and try again."
        );
        return;
    }
};
