import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "app/store";

interface SubTopic {
    name: string;
    slug: string;
    total: number;
    correct: number;
    wrong: number;
}

export interface Topic {
    name: string;
    subtopics: SubTopic[];
}

interface AuthState {
    topics: Topic[];
}

const initialState: AuthState = {
    topics: [],
};

export const dashboardSlice = createSlice({
    name: "dashboard",
    initialState,
    reducers: {
        setTopics: (state, action: PayloadAction<Topic[]>) => {
            state.topics = action.payload;
        },
    },
});

export const { setTopics } = dashboardSlice.actions;
export const selectTopics = (state: RootState) => state.dashboard.topics;

export default dashboardSlice.reducer;
