import Quiz from "containers/Quiz";
import React from "react";
import { RouteComponentProps } from "react-router-dom";

type QuizMatchParams = { topicSlug: string };

const QuizPage = ({ match }: RouteComponentProps<QuizMatchParams>) => {
    const [quizKey, setQuizKey] = React.useState<number>(0);
    const onNewQuiz = (e: React.MouseEvent<HTMLElement>) =>
        setQuizKey(quizKey + 1);
    const topicSlug = match.params.topicSlug;
    return (
        <Quiz
            topicSlug={topicSlug}
            key={`${topicSlug}-${quizKey}`}
            onNewQuiz={onNewQuiz}
        />
    );
};

export default QuizPage;
