import SignupForm from "containers/Auth/SignupForm";
import Button from "react-bootstrap/Button";
import { useHistory } from "react-router-dom";
import { ResultItem, ResultsCont, ReviewButton } from "./styled";

const Result = ({
    resetQuiz,
    totalCorrect,
    onReview,
    onNewQuiz,
    topicSlug,
}: {
    resetQuiz: () => void;
    totalCorrect: Number;
    onReview: () => void;
    onNewQuiz: (e: React.MouseEvent<HTMLElement>) => void;
    topicSlug?: string;
}) => {
    const isPage = Boolean(topicSlug);
    const history = useHistory();
    const onDashboard = (e: React.MouseEvent<HTMLElement>) =>
        history.push("/dashboard");
    return (
        <ResultsCont isPage={isPage}>
            <ResultItem>Total correct: {totalCorrect}</ResultItem>
            {!isPage && (
                <ResultItem>
                    <SignupForm isSignUp={true} isVertical={true} />
                </ResultItem>
            )}
            {isPage && (
                <>
                    <ResultItem>
                        <ReviewButton variant="link" onClick={onReview}>
                            Review all questions
                        </ReviewButton>
                    </ResultItem>
                    <ResultItem>
                        <ReviewButton variant="primary" onClick={onNewQuiz}>
                            New Quiz from same topic
                        </ReviewButton>
                    </ResultItem>
                    <ResultItem>
                        <ReviewButton variant="secondary" onClick={onDashboard}>
                            Back to Dashboard
                        </ReviewButton>
                    </ResultItem>
                </>
            )}
            <ResultItem>
                <Button onClick={resetQuiz} variant="link">
                    Try same questions again
                </Button>
            </ResultItem>
        </ResultsCont>
    );
};
export default Result;
