import { setUser } from "containers/Auth/authSlice";
import { setTopics } from "containers/Dashboard/dashboardSlice";
import { useState } from "react";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { transformServerErrors } from "utils/formUtils";
import { signUpAPI } from "../api";
import {
    FormCol,
    GetStartedButton,
    PasswordCol,
    SignUpFormCont,
} from "./styled";

type Inputs = {
    email: string;
    password: string;
};

const SignUpForm = ({
    isSignUp,
    isVertical,
}: {
    isSignUp: boolean;
    isVertical: boolean;
}) => {
    const [hidePasswordField, sethidePasswordField] = useState(!isVertical);
    const onEmailPress = (e: React.ChangeEvent<HTMLInputElement>) => {
        hidePasswordField && sethidePasswordField(false);
    };

    const history = useHistory();
    const dispatch = useDispatch();

    const {
        register,
        handleSubmit,
        errors,
        setError,
        formState: { isSubmitting },
    } = useForm();

    const onSubmit = async (data: Inputs) => {
        const { email, password } = data;
        try {
            const data = await signUpAPI(email, password, isSignUp);
            if (!data) return;
            dispatch(setUser(data.user));
            dispatch(setTopics(data.topics));

            history.push("/dashboard");
        } catch (error) {
            transformServerErrors(error.response.data, setError);
        }
    };
    const colSize = isVertical ? 12 : 3;
    return (
        <SignUpFormCont isVertical={isVertical}>
            <form method="get" onSubmit={handleSubmit(onSubmit)}>
                <Form.Group>
                    {Boolean(errors.non_field_errors) && (
                        <Row style={{ textAlign: "center" }}>
                            <Col>
                                <div className="text-danger">
                                    {errors.non_field_errors.message}
                                </div>
                            </Col>
                        </Row>
                    )}
                    <input
                        name={"non_field_errors"}
                        type={"hidden"}
                        ref={register}
                    />

                    <Row>
                        {!isVertical && <Col md={hidePasswordField ? 3 : 2} />}
                        <FormCol md={colSize}>
                            <Form.Control
                                type="text"
                                placeholder="Email"
                                name="email"
                                size="lg"
                                onChange={onEmailPress}
                                ref={register({
                                    required: true,
                                    pattern: {
                                        value: /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i,
                                        message:
                                            "Please enter a valid email address.",
                                    },
                                })}
                                isInvalid={errors.email}
                            />
                            {errors.email && (
                                <div className="text-danger">
                                    {errors.email.message}
                                </div>
                            )}
                        </FormCol>

                        <PasswordCol
                            md={colSize}
                            isHidden={hidePasswordField}
                            isVertical={isVertical}
                        >
                            <Form.Control
                                type="password"
                                placeholder="Password"
                                size="lg"
                                name="password"
                                ref={register({
                                    required: true,
                                    minLength: {
                                        value: 8,
                                        message:
                                            "Your password must be at least 8 characters",
                                    },
                                })}
                                isInvalid={errors.password}
                            />
                            {!hidePasswordField && errors.password && (
                                <div className="text-danger">
                                    {errors.password.message}
                                </div>
                            )}
                        </PasswordCol>
                        <FormCol md={colSize}>
                            <GetStartedButton
                                variant="primary"
                                size="lg"
                                type="submit"
                                isLoading={isSubmitting}
                                block={isVertical ? true : false}
                            >
                                {isSignUp ? "Signup with Email!" : "Login"}
                            </GetStartedButton>
                        </FormCol>
                    </Row>
                </Form.Group>
            </form>
        </SignUpFormCont>
    );
};

export default SignUpForm;
