import {
    hideLoginModal,
    selectShouldShowLoginModal,
} from "containers/Auth/authSlice";
import { useState } from "react";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { useDispatch, useSelector } from "react-redux";
import SignUpForm from "../SignupForm";

const LoginModal = ({ useLogin = true }: { useLogin?: boolean }) => {
    const [isLogin, setIsLogin] = useState(useLogin);
    const dispatch = useDispatch();

    const handleClose = () => {
        dispatch(hideLoginModal());
    };
    const shouldShow = useSelector(selectShouldShowLoginModal);

    const buttonTextFirst = isLogin
        ? "Don't have an account?"
        : "Already have account?";
    const buttonTextSecond = isLogin ? "Sign up instead" : "Login Instead";
    return (
        <Modal show={shouldShow} onHide={handleClose}>
            <Modal.Header closeButton closeLabel="Close" />
            <Modal.Body style={{ minHeight: "300px" }} key={isLogin.toString()}>
                <SignUpForm isSignUp={!isLogin} isVertical={true} />
                <Button
                    variant="text"
                    block={true}
                    onClick={() => setIsLogin(!isLogin)}
                >
                    {buttonTextFirst}

                    <span style={{ paddingLeft: "5px", color: "#004499" }}>
                        {buttonTextSecond}
                    </span>
                </Button>
            </Modal.Body>
        </Modal>
    );
};

export default LoginModal;
