import Card from "react-bootstrap/Card";
import CardColumns from "react-bootstrap/CardColumns";
import { md, sm, xs } from "responsive";
import styled from "styled-components";

const getDashboardCardColumnsStyles = ({ locked }: { locked: boolean }) => `
    opacity: ${locked ? 0.5 : 1};
`;

export const DashboardCardColumns = styled(CardColumns)`
    ${getDashboardCardColumnsStyles}
    @media (max-width: ${xs}px) {
        column-count: 1;
    }
    @media (min-width: ${sm}px) {
        column-count: 2;
    }
    @media (min-width: ${md}px) {
        column-count: 4;
    }
    padding-bottom: 50px;
`;

export const DashboardCard = styled(Card)`
    height: 260px;
    width: 260px !important;
    margin: 0 auto;
    text-align: center;
    border: 1px solid rgb(255, 255, 255);
    box-shadow: rgb(34 50 65 / 10%) 7px 8px 16px -6px;
    cursor: pointer;
    .card-body {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        font-size: 18px;
        font-weight: 16;
        color: #004488;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-direction: column;
    }
    :hover {
        background-color: #ffffef;
    }
`;

export const TopicTitle = styled.h3`
    padding: 20px 0 20px 0;
    font-weight: 500;
    color: #444;
`;
