import { setTopics } from "containers/Dashboard/dashboardSlice";
import React, { useEffect, useState } from "react";
import Container from "react-bootstrap/Container";
import Spinner from "react-bootstrap/Spinner";
import { useDispatch } from "react-redux";
import { getQuestions, Question, submitQuiz } from "./api";
import Cross from "./images/cross";
import Tick from "./images/tick";
import Result from "./Result";
import {
    AnswerCol,
    NextButton,
    OptionCont,
    OptionImage,
    QuestionImage,
    QuestionRow,
    QuestionText,
    StyledProgressBar,
} from "./styled";
import { randomizeQuestions } from "./utils";

interface Answered {
    questionId: number;
    answerId: number;
    isCorrect: boolean;
    correctAnswerId: number | null;
}

const Quiz = ({
    topicSlug = undefined,
    onNewQuiz = () => undefined,
}: {
    topicSlug?: string;
    onNewQuiz?: (e: React.MouseEvent<HTMLElement>) => void;
}) => {
    const dispatch = useDispatch();
    const [questions, setQuestions] = useState<Question[]>([]);
    const [sittingId, setSittingid] = useState<number | undefined>(undefined);
    const [questionIndex, setQuestionIndex] = useState<number>(0);
    const [answeredQuestions, setAnsweredQuestions] = useState<Answered[]>([]);
    const [ticked, setTicked] = useState<number | null>(null);
    const [crossed, setCrossed] = useState<number | null>(null);
    const [isReview, setIsReview] = useState<boolean>(false);

    const question = questions[questionIndex];
    const answeredQuestion = question
        ? answeredQuestions.find(
              (answeredQuestion) => answeredQuestion.questionId === question.id
          )
        : undefined;

    useEffect(() => {
        getQuestions(topicSlug).then((result) => {
            setQuestions(result.questions);
            setSittingid(result.id);
        });
    }, [topicSlug]);

    const nextQuestion = (answerId?: number) => {
        if (!question || ticked !== null || crossed !== null) return;
        if (isReview || !answerId) {
            return setQuestionIndex(questionIndex + 1);
        }
        const correctAnswer = question.answer_list.find(
            (answer) => answer.is_correct
        );
        const isCorrect = correctAnswer ? correctAnswer.id === answerId : false;

        const answered = {
            questionId: question.id,
            answerId,
            isCorrect,
            correctAnswerId: correctAnswer ? correctAnswer.id : null,
        };
        setAnsweredQuestions([
            answered,
            ...answeredQuestions.filter(
                (answer) => answer.questionId !== question.id
            ),
        ]);
        setTimeout(() => {
            const newQuestionIndex = questionIndex + 1;
            setQuestionIndex(newQuestionIndex);
            setTicked(null);
            setCrossed(null);
            if (
                questions.length > 0 &&
                newQuestionIndex === questions.length &&
                !isReview &&
                topicSlug &&
                answeredQuestions.length > 0 &&
                sittingId
            ) {
                const correctQuestionIds = answeredQuestions
                    .filter((answered) => answered.isCorrect)
                    .map((answered) => answered.questionId);
                submitQuiz(sittingId, correctQuestionIds).then(
                    (topics) => topics.length > 0 && dispatch(setTopics(topics))
                );
            }
        }, 2000);
        isCorrect ? setTicked(answerId) : setCrossed(answerId);
    };

    const resetQuiz = () => {
        setQuestions(randomizeQuestions(questions));
        setQuestionIndex(0);
        setAnsweredQuestions([]);
        setIsReview(false);
    };

    const onReview = () => {
        setIsReview(true);
        setQuestionIndex(0);
    };

    let content;

    if (questions.length > 0 && questionIndex === questions.length) {
        content = (
            <Result
                onNewQuiz={onNewQuiz}
                topicSlug={topicSlug}
                resetQuiz={resetQuiz}
                onReview={onReview}
                totalCorrect={
                    answeredQuestions.filter((answered) => answered.isCorrect)
                        .length
                }
            />
        );
    } else if (!question) {
        content = (
            <QuestionRow>
                <Spinner animation="border" />
            </QuestionRow>
        );
    } else {
        const progress =
            questions.length > 0
                ? ((questionIndex + 1) / questions.length) * 100
                : 0;
        content = (
            <React.Fragment>
                <QuestionRow>
                    <StyledProgressBar
                        now={progress}
                        label={`${questionIndex + 1} / ${questions.length}`}
                    />
                </QuestionRow>
                {isReview && (
                    <QuestionRow>
                        <NextButton
                            onClick={() => nextQuestion()}
                            variant="secondary"
                            size="sm"
                        >
                            Next
                        </NextButton>
                    </QuestionRow>
                )}
                <QuestionRow>
                    <QuestionText>{question.content}</QuestionText>
                    {question.image && <QuestionImage src={question.image} />}
                </QuestionRow>
                <QuestionRow>
                    {question.answer_list.map((answer) => {
                        const isTicked = !isReview
                            ? answer.id === ticked
                            : answeredQuestion &&
                              answer.id === answeredQuestion.correctAnswerId;
                        const isCrossed = !isReview
                            ? answer.id === crossed
                            : answeredQuestion &&
                              answer.id === answeredQuestion.answerId &&
                              answer.id !== answeredQuestion.correctAnswerId;
                        const colMd =
                            question.answer_list.length > 1
                                ? Math.floor(12 / question.answer_list.length)
                                : 6;
                        return (
                            <AnswerCol
                                md={colMd}
                                key={`${question.id}-${answer.id}`}
                            >
                                <OptionCont
                                    onClick={() => nextQuestion(answer.id)}
                                    forceHighlight={
                                        answeredQuestion &&
                                        answeredQuestion.answerId === answer.id
                                    }
                                    isReview={isReview}
                                >
                                    {answer.image && (
                                        <OptionImage
                                            isTransparent={Boolean(
                                                !isReview &&
                                                    (isTicked || isCrossed)
                                            )}
                                            src={answer.image}
                                        />
                                    )}
                                    {answer.content && (
                                        <div> {answer.content} </div>
                                    )}

                                    {isTicked && <Tick isReview={isReview} />}
                                    {isCrossed && <Cross isReview={isReview} />}
                                </OptionCont>
                            </AnswerCol>
                        );
                    })}
                </QuestionRow>
            </React.Fragment>
        );
    }
    return (
        <Container onClick={() => (isReview ? nextQuestion() : undefined)}>
            {content}
        </Container>
    );
};

export default Quiz;
