import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { AppThunk, RootState } from "app/store";
import { setTopics } from "containers/Dashboard/dashboardSlice";
import { bootstrapAPI, logOutAPI } from "./api";

export interface User {
    username: string;
    email: string;
    id?: number;
}

interface AuthState {
    user?: User;
    isLoginModalShown: boolean;
    isFetchingBootstrap?: boolean;
}

const initialState: AuthState = {
    user: undefined,
    isLoginModalShown: false,
    isFetchingBootstrap: undefined,
};

export const authSlice = createSlice({
    name: "auth",
    initialState,
    reducers: {
        setUser: (state, action: PayloadAction<User>) => {
            state.user = action.payload;
        },
        removeUser: (state) => {
            state.user = undefined;
        },
        showLoginModal: (state) => {
            state.isLoginModalShown = true;
        },
        hideLoginModal: (state) => {
            state.isLoginModalShown = false;
        },
        setIsFetchingBootstrap: (state, action: PayloadAction<boolean>) => {
            state.isFetchingBootstrap = action.payload;
        },
    },
});

export const {
    setUser,
    removeUser,
    showLoginModal,
    hideLoginModal,
    setIsFetchingBootstrap,
} = authSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.auth.value)`
export const selectUser = (state: RootState) => state.auth.user;
export const selectShouldShowLoginModal = (state: RootState) =>
    state.auth.isLoginModalShown && !state.auth.user;
export const selectIsFetchingBootstrap = (state: RootState) =>
    state.auth.isFetchingBootstrap;

export const getBootstrapData = (): AppThunk => async (dispatch) => {
    const token = localStorage.getItem("token");
    if (!token) return;
    dispatch(setIsFetchingBootstrap(true));
    const data = await bootstrapAPI(token);
    if (data) {
        dispatch(setUser(data.user));
        dispatch(setTopics(data.topics));
    }
    dispatch(setIsFetchingBootstrap(false));
};

export const logOut = (): AppThunk => (dispatch) => {
    logOutAPI();
    dispatch(removeUser());
};
export default authSlice.reducer;
