import {
    selectIsFetchingBootstrap,
    selectUser,
} from "containers/Auth/authSlice";
import SignUpForm from "containers/Auth/SignupForm";
import Quiz from "containers/Quiz";
import React from "react";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Spinner from "react-bootstrap/esm/Spinner";
import Row from "react-bootstrap/Row";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import {
    FormContainer,
    Header,
    HeadingContainer,
    QuizContainer,
    SubHeading,
} from "./styled";

const HomePage = () => {
    const history = useHistory();
    const user = useSelector(selectUser);
    const isFetching = useSelector(selectIsFetchingBootstrap);
    if (user) {
        history.push("/dashboard");
    }

    if (isFetching) {
        return <Spinner animation="grow" />;
    }
    return (
        <Container>
            <Row>
                <HeadingContainer>
                    <Header>Be confident in Machine Learning.</Header>
                    <SubHeading>
                        Understand the key concepts of Machine Learning with
                        100's of quizzes.
                    </SubHeading>
                    <FormContainer>
                        <SignUpForm isSignUp={true} isVertical={false} />
                    </FormContainer>
                </HeadingContainer>
            </Row>
            <Row>
                <Col sm={12}>
                    <QuizContainer>
                        <Quiz />
                    </QuizContainer>
                </Col>
            </Row>
        </Container>
    );
};

export default HomePage;
