import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import ProgressBar from "react-bootstrap/ProgressBar";
import Row from "react-bootstrap/Row";
import styled from "styled-components";

export const AnswerCol = styled(Col)`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const getOptionContStyles = ({
    forceHighlight,
    isReview,
}: {
    forceHighlight?: boolean;
    isReview: boolean;
}) => {
    if (!isReview) {
        return `
            :hover {
                background-color: #ffffef;
                opacity: 0.5;
            }
            cursor: pointer;
        `;
    } else {
        return `
            ${forceHighlight ? "background-color: #ffffef;" : ""}
        `;
    }
};

export const OptionCont = styled.div`
    ${getOptionContStyles}
    min-height: 200px;
    display: flex;
    width: 220px;
    padding: 0 10px 0 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(255, 255, 255);
    box-shadow: rgb(34 50 65 / 10%) 0px 6px 20px 0px;
    margin-bottom: 50px;
`;

export const OptionImage = styled.img`
    height: 200px;
    max-width: 200px;
    padding: 10px;
    margin-bottom: 10px;
    opacity: ${(props: { isTransparent: boolean }) =>
        props.isTransparent ? "0.2" : "1"};
`;

export const QuestionImage = styled.img`
    max-height: 250px;
    padding: 10px;
    margin: 0 auto;
    max-width: 400px;
    margin-bottom: 40px;
`;

export const QuestionText = styled.div`
    padding: calc(10px + 10vmin); 10px 40px 10px;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    font-weight: 500;
    color: #666;
    width: 100%;
`;

export const StyledProgressBar = styled(ProgressBar)`
    width: 100%;
    height: 30px;
`;

export const QuestionRow = styled(Row)`
    text-align: center;
`;

export const NextButton = styled(Button)`
    margin: 30px auto 0 auto;
    min-width: 150px;
    border-radius: 0;
`;
