import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { selectTopics, Topic } from "./dashboardSlice";
import { DashboardCard, DashboardCardColumns, TopicTitle } from "./styled";

const Dashboard = () => {
    const topics: Topic[] = useSelector(selectTopics);
    return (
        <Container>
            {topics.map((topic) => (
                <div key={topic.name}>
                    <TopicTitle>{topic.name}</TopicTitle>
                    <DashboardCardColumns>
                        {topic.subtopics.map((subtopic) => (
                            <Link
                                to={`/quiz/${subtopic.slug}`}
                                key={subtopic.slug}
                            >
                                <DashboardCard>
                                    <Card.Body>
                                        <div>{subtopic.name}</div>
                                        <div
                                            style={{
                                                marginTop: "30px",
                                                fontSize: "11px",
                                            }}
                                        >
                                            <div>
                                                Total Questions:{" "}
                                                {subtopic.total}
                                            </div>
                                            <div>
                                                Correct Answers:{" "}
                                                {subtopic.correct}
                                            </div>
                                            <div>
                                                Wrong Answers: {subtopic.wrong}
                                            </div>
                                        </div>
                                    </Card.Body>
                                </DashboardCard>
                            </Link>
                        ))}
                    </DashboardCardColumns>
                </div>
            ))}
        </Container>
    );
};

export default Dashboard;
