import Button from "components/Button";
import Col from "react-bootstrap/esm/Col";
import styled from "styled-components";

export const GetStartedButton = styled(Button)`	
    min-width: 300px	
    text-align: center;	
`;

export const SignUpFormCont = styled.div`
    text-align: left;
    ${(props: { isVertical: boolean }) => props.isVertical && "width: 100%;"}
`;

const getPasswordColStyles = ({
    isVertical,
    isHidden,
}: {
    isHidden: boolean;
    isVertical: boolean;
}) => `	
    max-width: ${isHidden ? "0" : "100%"};	
    padding: ${isHidden ? "0" : "0 15px 0 15px"};	
    input {	
        display: ${isHidden ? "none" : "inline"};	
    }	
`;

const getFormColStyles = ({
    isVertical,
    isHidden = false,
}: {
    isVertical: boolean;
    isHidden?: boolean;
}) => `	
    margin-bottom: 20px;
`;

export const PasswordCol = styled(Col)`
    transition: all 1.5s ease;
    ${getPasswordColStyles}
    ${getFormColStyles}
`;

export const FormCol = styled(Col)`
    text-align: center;
    ${getFormColStyles}
`;
