import React from "react";
import { ButtonProps } from "react-bootstrap/Button";
import Spinner from "react-bootstrap/Spinner";
import { SpinnerCont, StyledBootstrapButton } from "./styled";

const Button = ({
    isLoading,
    children,
    disabled,
    ...remainingProps
}: {
    isLoading?: boolean;
    children: any;
    disabled?: boolean;
} & ButtonProps) => (
    <StyledBootstrapButton {...remainingProps} disabled={isLoading || disabled}>
        {isLoading && (
            <SpinnerCont>
                <Spinner size="sm" animation="border" />
            </SpinnerCont>
        )}
        <span style={{ margin: "0 auto" }}>{children}</span>
    </StyledBootstrapButton>
);

export default Button;
